/*Root*/
.App {
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding-bottom: -5px;
  height: 98vh;
  min-height: 400px;
}

/*Classes*/
.overlay {
  background-image:
    url("http://moziru.com/images/sparkles-clipart-transparent-background-9.jpg");
  background-size: cover;
}
.form {
  opacity: 100%;
  border: 10px solid black;
  border-radius: 125px;
  margin: 0 auto;
  background-color: white;
  height: 80vh;
  min-height: 425px;
}

.inputField {
  text-align: center;
  border: 2px solid black;
  font-size: .5em;
  margin-top: .1em;
  height:22px;
}

/*IDs*/
.content {
  padding-top: 1rem;
  height: 90vh;
  /*min-height: 800px;*/
}

/*Elements*/
form {
  width: 40%;
  font-size: 40px;
  height: 100%;
}
h1 {
  font: 400 1.5em 'Pacifico', Helvetica, sans-serif;
  color: #2b2b2b;
  text-shadow: 3px 3px 0px rgba(0,0,0,0.1), 7px 7px 0px rgba(0,0,0,0.05);
  line-height: 1.34em;
  height: 30vh;
}

/* cyrillic */
@font-face {
  font-family: 'Pacifico';
  font-style: normal;
  font-weight: 400;
  src: local('Pacifico Regular'), local('Pacifico-Regular'), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6D6MmBp0u-zK4.woff2) format('woff2');
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}
/* vietnamese */
@font-face {
  font-family: 'Pacifico';
  font-style: normal;
  font-weight: 400;
  src: local('Pacifico Regular'), local('Pacifico-Regular'), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6I6MmBp0u-zK4.woff2) format('woff2');
  unicode-range: U+0102-0103, U+0110-0111, U+1EA0-1EF9, U+20AB;
}
/* latin-ext */
@font-face {
  font-family: 'Pacifico';
  font-style: normal;
  font-weight: 400;
  src: local('Pacifico Regular'), local('Pacifico-Regular'), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6J6MmBp0u-zK4.woff2) format('woff2');
  unicode-range: U+0100-024F, U+0259, U+1E00-1EFF, U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}
/* latin */
@font-face {
  font-family: 'Pacifico';
  font-style: normal;
  font-weight: 400;
  src: local('Pacifico Regular'), local('Pacifico-Regular'), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6H6MmBp0u-.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}




h2 {
  padding-top: 0%;
  font-size: auto;
  width: 100%;
  line-height: 5px;
  word-wrap: break-word;
}
p {
  font-size: .3em;
}
input {
  width: 40%;

}
footer {
  font-size: 10px;
  text-align: center;
  color: white;
  background-color: rgba(0,0,0,.7);
}

@media only screen and (max-height: 500px){

  p {
    display: none;
  }


}
